<template>
  <div class="container" v-cloak>
    <!-- 头部导航 -->
    <navbar class="navbar">
      <div class="back" slot="back">
        <i class="iconfont iconjiantou2" @click="$router.back()"></i>
      </div>
      <div class="title" slot="title">
        <i class="iconfont icon-logo1"></i>
      </div>
      <div
        slot="info"
        class="info"
        @click="hasfollow"
        :class="articel.has_follow ? '':'guanzhu' "
      >{{articel.has_follow? "已关注":"关注"}}</div>
    </navbar>
    <!-- 头部标题 -->
    <header>
      <h4>{{articel.title}}</h4>
      <div class="sub-title">
        <span>{{articel.user.nickname}}</span>
        <span>{{moment(articel.user.create_date).format(`YYYY-MM-DD hh:mm:ss`)}}</span>
      </div>
    </header>
    <!--文章内容  -->
    <article>
      <p v-html="articel.content" v-if="articel.type==1"></p>
      <video
        :src="$axios.defaults.baseURL+articel.content"
        v-if="articel.type==2"
        controls
        autoplay
      ></video>
      <!-- 点赞 -->
      <div class="option">
        <div class="opt-item" @click="dianzan">
          <i class="iconfont icondianzan" :class="{dianzan:articel.has_like}"></i>
          <span>{{articel.has_like? 1:""}}</span>
        </div>
        <div class="opt-item">
          <i class="iconfont iconweixin"></i>
          <span>微信</span>
        </div>
      </div>
    </article>
    <!-- 精彩跟帖 -->
    <section>
      <comments></comments>
    </section>
    <!-- 底部评论 -->
    <footer class="myfooter">
      <div class="comment">
        <input type="text" name id placeholder="写点什么吧" />
      </div>
      <div class="icon-info">
        <!-- 评论的条数 -->
        <router-link :to="'/comment/'+this.$route.params.id">
          <div class="icons pinglun">
            <span class="iconfont iconpinglun-"></span>
            <div :class="{active:articel.comment_length>=1}"></div>
          </div>
        </router-link>
        <!-- 如果当前是收藏的状态显示一个红色的按钮 -->
        <div class="icons">
          <span class="iconfont iconshoucang" :class="{active:articel.has_star}" @click="collect"></span>
        </div>
        <!-- 这个按钮是一个装饰用的，微信分享需要企业的资质 -->
        <div class="icons">
          <span class="iconfont iconfenxiang"></span>
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
import navbar from "@/components/navbar";
// 引入第三方的日期格式处理的工具库
import moment from "moment";
// 导入新闻列表组件
import comments from "@/components/comments";
export default {
  mounted() {
    const { token } = JSON.parse(localStorage.getItem("user_info")) || "";
    this.token = token;
    // console.log(this.$route.params.id);
    // 根据id，获取文章详情信息
    this.$axios({
      url: `/post/${this.$route.params.id}`,
      method: "get"
    }).then(res => {
      const { data } = res.data;
      // 替换文章内容的本地路径，把locahost:3000 改成线上地址
      data.conent = data.content.replace(
        /http:\/\/localhost:3000/gi,
        this.$axios.defaults.baseURL
      );
      this.articel = data;
      // console.log(this.articel);
    });
  },
  data() {
    return {
      token: "",
      articel: {
        user: {}
      },
      moment
    };
  },
  methods: {
    //   关注和取消关注
    hasfollow() {
      let url = "";
      // 先判断是关注还是取消，然后取反操作
      if (this.articel.has_follow) {
        url = `/user_unfollow/${this.articel.user.id}`;
      } else {
        url = `/user_follows/${this.articel.user.id}`;
      }
      this.$axios({
        url,
        headers: {
          Authorization: this.token
        }
      }).then(res => {
        console.log(this.articel);
        this.articel.has_follow = !this.articel.has_follow;
        this.$toast(this.articel.has_follow ? "关注成功" : "取消关注成功");
      });
    },
    dianzan() {
      let url;
      // 先判断已经是点赞还是未点赞，然后取反操作
      if (!this.articel.has_like) {
        url = `/post_like/${this.$route.params.id}`;
      } else {
        url = `/post_like/${this.$route.params.id}`;
      }
      this.$axios({
        url,
        headers: {
          Authorization: this.token
        }
      }).then(res => {
        console.log(res);
        this.articel.has_like = !this.articel.has_like;
        // this.$toast(this.articel.has_like ? "成功" : "取消关注成功");
      });
    },
    // 文章收藏功能
    collect() {
      let url;
      // 先判断已经是点赞还是未点赞，然后取反操作
      if (this.articel.has_star) {
        url = `/post_star/${this.$route.params.id}`;
      } else {
        url = `/post_star/${this.$route.params.id}`;
      }
      this.$axios({
        url,
        headers: {
          Authorization: this.token
        }
      }).then(res => {
        console.log(res);
        this.articel.has_star = !this.articel.has_star;
        this.$toast(this.articel.has_star ? "收藏成功" : "取消收藏成功");
      });
    }
  },
  components: {
    navbar,
    comments
  }
};
</script>

