<template>
  <div class="container">
    <!-- 评论区 -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      :immediate-check="false"
    >
      <div class="comment" v-for="(item,index) in comment" :key="index">
        <div class="user-info">
          <div class="header">
            <img :src="$axios.defaults.baseURL+item.user.head_img" alt />
          </div>
          <div class="text">
            <h4>{{item.user.nickname}}</h4>
            <div class="content">{{item.content}}</div>
            <div>
              <span class="time">{{moment(item.create_date).fromNow()}}</span>
              <div class="apply">回复</div>
            </div>
          </div>
        </div>
        <commentitem v-if="item.parent" :data="item.parent"></commentitem>
      </div>
    </van-list>
  </div>
</template>

<script>
import navbar from "@/components/navbar";
import moment from "moment";
import commentitem from "@/components/comment-item";
export default {
  data() {
    return {
      comment: "",
      moment,
      pid: "",
      // 是否正在加载中
      loading: false,
      // 数据是否加载完毕
      finished: false,
      // 请求的页数
      pageIndex: 1,
      // 请求的条数
      pageSize: 5
    };
  },

  // 获取评论列表
  mounted() {
    const id = this.$route.params.id;
    this.pid = id;
    this.getcomment();
  },
  methods: {
    onLoad() {
      this.getcomment();
    },
    getcomment() {
      this.$axios({
        url: `/post_comment/${this.pid}`,
        method: "get",
        params: {
          pageIndex: this.pageIndex,
          pageSize: this.pageSize
        }
      })
        .then(res => {
          const { data } = res.data;
          this.comment = [...data, ...this.comment];
          this.pageIndex++;
          // 加载状态结束
          this.loading = false;
          // 当评论的条数小于每页的条数时，证明可以是加载完
          if (data.length < this.pageSize) {
            this.finished = true;
          }
          console.log(this.comment);
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  components: {
    navbar,
    commentitem
  }
};
</script>

